import React from 'react'
import Message from './Message'
import ReactDOMServer from "react-dom/server"
import './Chat.css'

function add(username, message) {
  const newMessage = ReactDOMServer
    .renderToStaticMarkup(<Message username={`${username}`} message={`${message}`} />)
  document.getElementById('messages').innerHTML += newMessage
}

function handleSubmit(e) {
  e.preventDefault()
  const mensaje = document.getElementById('mensaje')
  add('', mensaje.value)
  mensaje.value = ''
}

function Chat() {
  return (
    <div className="container">
      <div className="chat-box">
        <div id="messages">
          <Message username="matthias" message="1" />
          <Message username="matthias" message="2" />
          <Message username="matthias" message="3" />
          <Message username="matthias" message="4" />
          <Message username="matthias" message="5" />
          <Message username="matthias" message="6" />
          <Message username="matthias" message="7" />
          <Message username="matthias" message="8" />
          <Message username="matthias" message="9" />
          <Message username="matthias" message="10" />
          <Message username="matthias" message="11" />
          <Message username="matthias" message="12" />
          <Message username="matthias" message="13" />
          <Message username="matthias" message="14" />
          <Message username="matthias" message="15" />
        </div>
      </div>
      <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
        <input type="text" id="mensaje" placeholder="Escribe un mensaje aquÃ­"></input>
        <button type ="submit">Enviar Mensaje</button>
      </form>
    </div>
  )
}
export default Chat
