const colors = {
	red: '#cc4e4e',
	yellow: '#f5e15f',
	green: '#3edb3b',
	blue: '#43dedb',
}

function getColor() {
	let randomNumber = Math.floor(Math.random() * Object.keys(colors).length)
	return colors[Object.keys(colors)[randomNumber]]
}
function Message(props) {
	return (
		<div style={styles.message}>
			<span style={{ color: `${getColor()}` }}>{props.username}: </span>
			<span style={{ color: "white" }}>{props.message}</span>
		</div>
	)
}


const styles = {
	message: {
		padding: '5px'
	}
}
export default Message
